import React, {useState, useEffect} from 'react'
import PropTypes from 'prop-types'

import './Emails.scss'

const Emails = ({title, list, actionButtonTitle, onClickToggle}) => {
    const [isMobile, setIsMobile] = useState(false);

    const handleResize = (event) => {
        console.log(event.target.innerWidth)

        if (event.target.innerWidth < 500) {
            setIsMobile(true)
        } else {
            setIsMobile(false)
        }
    }

    const handleButtonClick = (id) => {
        console.log('id from emails ->', id);
        onClickToggle(id)
    }

    useEffect(() => {
        window.addEventListener('resize', handleResize);

        if (list && list.length === 0) {
            alert('List is empty')
        }

        return () => {  //     ,   ,     ,       .
            window.removeEventListener('resize', handleResize);
        }
    },[list])

    // console.log(isMobile);

    return (
        <>
            <h3>{title}</h3>
            <ul className="emails-list">
                {list.map(email => (
                    <li className="list-item" key={email.id}>
                        <span>
                            <i>{email.title}</i> (<a href={`mailto:${email.from}`}>{email.from}</a>)
                        </span>
                        {/* {isMobile ? null :
                            <button onClick={event => {this.handleButtonClick(email.id)}}>{actionButtonTitle}</button>
                        } */}
                        {!isMobile && <button type="button" className="btn btn--text" onClick={event => {
                            handleButtonClick(email.id)
                        }}>{actionButtonTitle}</button>}

                    </li>
                ))}
            </ul>
        </>
    )
}

Emails.propTypes = {
    // obj: PropTypes.shape({
    //     title: PropTypes.string
    // }),
    list: PropTypes.arrayOf(PropTypes.shape({
        topic: PropTypes.string,
        from: PropTypes.string,
    }))
}

export default Emails
