import React, {useEffect, useState} from 'react';
import {useParams} from "react-router-dom";
import {createBrowserHistory} from "history";

import {sendRequest} from "../../helpers/sendRequest";
import {API_KEY_3, API_URL, IMG_URL} from "../../configs/API";

import {ReactComponent as LeftArrow} from "./images/left-arrow.svg";
import {ReactComponent as PosterBroken} from './images/poster-broken.svg'

import './MoviePage.scss';

const MoviePage = () => {
	const [movie, setMovie] = useState({})
	const {movieID} =  useParams();
	const history = createBrowserHistory({ window });

	useEffect(() => {
		sendRequest(`${API_URL}/movie/${movieID}?api_key=${API_KEY_3}`)
			.then(data => {
				setMovie(data)

				console.log('MOVIE',data);
			})
	}, [movieID])

	const clickBack = () => {
		history.back()
	}

	const {
		backdrop_path,
		poster_path,
		original_title,
		title,
		genres,
		runtime,
		overview,
		release_date
	} = movie;

	return (
		<>
			{movie && (
				<header className="page-movie-header">
					<div className="header-bg" style={{backgroundImage: `url(${IMG_URL}${backdrop_path})`}} />
					<div className="container">
						<div className="header-wrapper">
							<a href="#" className="btn-back" onClick={clickBack}><LeftArrow /></a>
							<div className="header-poster">
								{ poster_path !== null ? (<img src={`${IMG_URL}${poster_path}`} alt={title}/> ) : ( <PosterBroken /> )}
							</div>
							<div className="header-content">
								<p className="movie-name">{title}</p>
								<p className="movie-subname"><i>{original_title}</i></p>
								<p className="movie-info">
									<span className="genres">{genres && genres.map((item,index) => index === genres.length-1 ? ( <span key={index}>{item.name}</span> ) : (<span key={index}>{item.name},</span>))}</span>
									{runtime && <span className="runtime">{runtime}</span>}
								</p>
								<p className="movie-overview">
									{overview}
								</p>
								<p className="movie-release">
									Release date: {release_date}
								</p>
							</div>
						</div>
					</div>
				</header>
			)}
		</>
	);
};

export default MoviePage;
