import React from 'react';
import PropTypes from 'prop-types';
import { Link } from 'react-router-dom';

import {ReactComponent as Logo} from './images/logo.svg';

import './Header.scss';

const Header = (props) => {
    const {username, age, user, onClickLogout} = props;

    const isAdult = age >= 18

    const handleClick = (event) => {
        console.log('logout');
        onClickLogout()
    }

    return (
        <header className="header">
            <div className="container">
                <div className="header__wrapper">
                    <div className="header__logo">
                        <a href="/" className="logo">
                            <Logo/>
                        </a>
                    </div>
                    {!user && <div className="header__title">
                        <h2>{props.title}</h2>
                    </div>}
                    {!user && <div className="header__right">
                        <div className="header__user">
                            {username && (
                                <p><Link to='/user'>{username}</Link> - {isAdult ? (<b>adult</b>) : (<i>young</i>)}</p>
                            )}
                        </div>
                        {username && <Link to='/inbox' className="btn btn--text user-inbox">Inbox</Link>}
                        {username && <button className="btn btn--primary" onClick={handleClick}>Logout</button>}
                    </div>}
                </div>
            </div>
        </header>
    )
}

Header.propTypes = {
    username: PropTypes.string,
    title: PropTypes.string,
    age: PropTypes.number,
    onClickLogout: PropTypes.func.isRequired,
}

Header.defaultProps = {
	title: 'Hello from DAN.IT',
}

export default Header;
