import { createReducer } from '@reduxjs/toolkit';

import * as actions from './actions';

const defaultState = {
  userAge: 24,
  userName: "Dan",
  movieSlider: {},
  tvSlider: {},
  movie: {},
  tv: {},
  loading: true
}

export default createReducer(defaultState, {
  [actions.setIncreaseAge]: (state) => {
    state.userAge += 1;
  },
  [actions.setDecreaseAge]: (state) => {
    state.userAge -= 1; 
  },
  [actions.setMovieSlider]: (state, {payload}) => {

    state.movieSlider = payload;
  },
  [actions.setLoading]: (state, {payload}) =>{
    state.loading = payload;
  }

})


