import { createAction } from '@reduxjs/toolkit';

import {sendRequest} from '../helpers/sendRequest'
import {API_URL, IMG_URL, API_KEY_3} from '../configs/API'

export const setIncreaseAge = createAction("SET_INCREASE_AGE")

export const setDecreaseAge = createAction("SET_DECREASE_AGE")

export const setMovieSlider = createAction("SET_MOVIE_SLIDER")

export const setLoading = createAction("SET_LOADING")

export const fetchMovieSlider = () => (dispatch) => {
    dispatch(setLoading(true))

    return sendRequest(`${API_URL}/discover/movie?api_key=${API_KEY_3}`)
			.then(data => {
                // console.log('setMovieSlider',data);
				dispatch(setMovieSlider(data));
                dispatch(setLoading(false));
			})
}
