import React from 'react'
import PropTypes from 'prop-types'

import Emails from "../Emails";

const Inbox = (props) => {
    const {name, toggleEmailStatus, notReadEmails, readEmails} = props;

    return (
        <div className="container">
            {name && (
                <>
                    <Emails
                        title={<span style={{color: 'green'}}>Inbox</span>}
                        list={notReadEmails}
                        onClickToggle={toggleEmailStatus}
                        actionButtonTitle="Mark as read"
                    />
                    <Emails
                        title="Archive"
                        list={readEmails}
                        onClickToggle={toggleEmailStatus}
                        actionButtonTitle="Mark as unread"
                    />
                </>
            )}
        </div>
    )
}


Inbox.propTypes = {
    name: PropTypes.string,
    notReadEmails: PropTypes.array,
    readEmails: PropTypes.array,
    toggleEmailStatus: PropTypes.func.isRequired,
}

export default Inbox
