import React,{useEffect,useState} from 'react'
import {Route, Routes} from "react-router-dom";

import Header from './components/Header/Header'
import Footer from './components/Footer/Footer';
import UserBlock from "./components/UserBlock/UserBlock";
import Inbox from "./components/Inbox/Inbox";
import Movies from './components/Movies';
import TVPage from './components/TVPage';
import MoviePage from './components/MoviePage';
import NotPage from "./components/NotPage";

import getEmails from './api/getEmails';

import './App.scss';

const App = () => {
	const [title, setTitle] = useState('Hello React');
	const [user, setUser] = useState(false);
	const [userName, setUserName] = useState('Dan');
	const [userAge, setUserAge] = useState(24);
	const [emails, setEmails] = useState([]);

	useEffect(() => {
		getEmails().then(emails => {
			setEmails(emails)
		});
	},[])

	const updateTitle = () => {
		setTitle('Hello React, again')
	}

	const increment = () => {
		setUserAge(userAge + 1)
	}

	const decrement = () => {
		setUserAge(userAge === 0 ? userAge : userAge - 1)
	}

	const logout = () => {
		setUser(true)
	}

	const toggleEmailStatus = (id) => {
		// console.log('from App ->', id);
		const newEmails = emails.map(email => {
			if (email.id === id) {
				return {...email, isRead: !email.isRead}
			}
			return email
		})
		setEmails(newEmails)
	}

	const readEmails = emails.filter(email => email.isRead)
	const notReadEmails = emails.filter(email => !email.isRead)

	return (
		<div className="page__wrapper">
			<Header
				username={userName}
				age={userAge}
				user={user}
				onClickLogout={logout}
				title={title}
			/>
			<main className="main">
				<Routes>
					<Route path='/tmdb' element={<Movies/>}/>
					<Route path='/user' element={
						<UserBlock
							name={userName}
							age={userAge}
							increment={increment}
							decrement={decrement}
							updateTitle={updateTitle}
						/>
					} />
					<Route path='/inbox' element={
						<Inbox
							name={userName}
							toggleEmailStatus={toggleEmailStatus}
							notReadEmails={notReadEmails}
							readEmails={readEmails}
						/>
					} />
					<Route path="/movie/:movieID" element={
						<MoviePage />
					} />
					{/* <Route path='/tv/:tvID' element={<TVPage/>}/> */}
					<Route path='/tv'>
						<Route path=':tvID' element={<TVPage/>} />
					</Route>
					<Route path='*' element={<NotPage/>}/>
				</Routes>
			</main>
			<Footer/>
		</div>
	)
}

export default App;
