import React, {useState, useEffect} from 'react'
import {useParams} from 'react-router-dom';
import {createBrowserHistory} from "history";

import {sendRequest} from "../../helpers/sendRequest";
import {API_KEY_3, API_URL, IMG_URL} from "../../configs/API";

import {ReactComponent as LeftArrow} from './images/left-arrow.svg'
import {ReactComponent as Broken} from './images/image-broken.svg'
import {ReactComponent as PosterBroken} from './images/poster-broken.svg'

import './TVPage.scss'

const TVPage = () => {
	const [moviesTv, setMoviesTv] = useState({})
	const {tvID} = useParams();
	const history = createBrowserHistory({window});

	useEffect(() => {
		sendRequest(`${API_URL}/tv/${tvID}?api_key=${API_KEY_3}`)
			.then(data => {
				setMoviesTv(data)
			})
	}, [tvID])

	const clickBack = () => {
		history.back();
	}

	const {
		backdrop_path,
		poster_path,
		name,
		original_name,
		genres,
		episode_run_time,
		overview,
		created_by,
		seasons,
		last_episode_to_air,
		next_episode_to_air
	} = moviesTv;
//   console.log('last_episode_to_air',last_episode_to_air);
	return (
		<>
			<header className="page-movie-header">
				<div className="header-bg" style={{backgroundImage: `url(${IMG_URL}${backdrop_path})`}}/>
				<div className="container">
					<div className="header-wrapper">
						<span className="btn-back" onClick={clickBack}><LeftArrow/></span>
						<div className="header-poster">
							<img src={`${IMG_URL}${poster_path}`} alt={name}/>
						</div>
						<div className="header-content">
							<p className="movie-name">{name}</p>
							<p className="movie-subname"><i>{original_name}</i></p>
							<p className="movie-info">
                                <span className="genres">
                                    {genres && genres.map(({name}, index) => index === genres.length-1 ? ( <span key={index}>{name}</span> ) : ( <span key={index}>{name},</span> ))}
                                </span>
								{episode_run_time && <span className="runtime">{episode_run_time}</span>}
							</p>
							<p className="movie-overview">
								{overview}
							</p>
							<p className="movie-created">
								{created_by && created_by.map(({name}, index) => <span key={index}>{name} <br/> Creator</span>)}
							</p>
						</div>
					</div>
				</div>
			</header>
			<div className="page-movie-content">
				<div className="container">
					<p className="content-title">Seasons</p>
					{/* { seasons {air_date,episode_count,name,poster_path} } */}
					<div className="cards-seasons">
						{
							seasons && seasons.map(({air_date, episode_count, name, poster_path}, index) => {
								return (
									<div className="card" key={index}>
										<div className="card-post">
											{poster_path !== null ? ( <img src={`${IMG_URL}${poster_path}`} alt={name}/>
												) : (
													<PosterBroken />
												)
											}
										</div>
										<div className="card-content">
											<div className="card-name">{name}</div>
											<div className="card-info">
												<span className="card-date">{air_date}</span> | <span
												className="card-episode">{episode_count} episodes</span>
											</div>
										</div>
									</div>
								)
							})
						}

					</div>
					<p className="content-title">Episodes</p>
					<div className="cards-episodes">
						{last_episode_to_air && (
							<div className="episode-item episodes-last">
								<div className="episode-wrapper">
									<div className="episodes-post">
										{last_episode_to_air.still_path !== null ? (<img src={`${IMG_URL}${last_episode_to_air.still_path}`} alt={last_episode_to_air.name}/> ) : (
											<Broken />
										)}
									</div>
									<div className="episodes-content">
										<p className="episodes-name">Episodes name: {last_episode_to_air.name}</p>
										<p className="episodes-date"><i>Episodes
											date: {last_episode_to_air.air_date}</i></p>
										<p className="episodes-info">Season: {last_episode_to_air.season_number} Episode: {last_episode_to_air.episode_number} Runtime: {last_episode_to_air.runtime}</p>
										<p className="episodes-overview">{last_episode_to_air.overview}</p>
									</div>
								</div>
							</div>
						)}
						{next_episode_to_air && (
							<div className="episode-item episodes-next">
								<div className="episode-wrapper">
									<div className="episodes-post">
										{next_episode_to_air.still_path !== null ? (<img src={`${IMG_URL}${next_episode_to_air.still_path}`} alt={next_episode_to_air.name}/> ) : (
											<Broken />
										)}
									</div>
									<div className="episodes-content">
										<p className="episodes-name">Episodes name: {next_episode_to_air.name}</p>
										<p className="episodes-date"><i>Episodes
											date: {next_episode_to_air.air_date}</i></p>
										<p className="episodes-info">Season: {next_episode_to_air.season_number} Episode: {next_episode_to_air.episode_number} Runtime: {next_episode_to_air.runtime}</p>
										<p className="episodes-overview">{next_episode_to_air.overview}</p>
									</div>
								</div>
							</div>
						)}
					</div>
				</div>
			</div>
		</>
	)
}

export default TVPage
