import React from 'react'
import PropTypes from 'prop-types'
import { useSelector, useDispatch } from 'react-redux';

import { selectUserAge, selectUserName } from '../../store/selectors'
import { setIncreaseAge, setDecreaseAge } from '../../store/actions'

import './UserBlock.scss';

const UserBlock = ({updateTitle}) => {
    const dispatch = useDispatch()
    const name = useSelector(selectUserName)
    const age = useSelector(selectUserAge)

    const updateHeaderTitle = (event) => {
        event.target.disabled = true;
        updateTitle();
    }

    const increment = () => {
        dispatch(setIncreaseAge())
    }

    const decrement = () => {
        dispatch(setDecreaseAge())
    }

    console.log(age);

    return (
        <div className="container">
            {name && (
                <>
                    <div className="user__block">
                        {/* {name ? (<p>It's {name} and I am {age} years old.</p>) : null} */}
                        <p>It's {name} and I am {age} years old.</p>
                    </div>
                    <div className="buttons-wrapper">
                        <button type="button" className="btn btn--secondary" onClick={increment}>
                            Increment
                        </button>
                        <button type="button" className="btn btn--secondary" onClick={decrement}>
                            Decrement
                        </button>
                        <button type="button" className="btn btn--secondary" onClick={(e) => updateHeaderTitle(e)}>
                            Update title
                        </button>
                    </div>
                </>
            )}
        </div>
    )
}

UserBlock.propTypes = {
    updateTitle: PropTypes.func.isRequired,
}

export default UserBlock
